import React, { useEffect, useState } from "react";
import { useRouter } from "next/router";

const ProductDetail: React.FC = () => {
  const [product, setProduct] = useState<any>(null);
  const router = useRouter();

  useEffect(() => {
    const storedProduct = localStorage.getItem("selectedProduct");

    if (storedProduct) {
      setProduct(JSON.parse(storedProduct));
    } else {
      router.push("/");
    }

    return () => {
      localStorage.removeItem("selectedProduct");
    };
  }, [router]);

  if (!product) return <p>Carregando detalhes do produto...</p>;

  return (
    <div className="container p-4">
      <h1 className="mb-3">{product.nome}</h1>
      <p>
        <strong>Categoria:</strong> {product.categoria}
      </p>
      <p>
        <strong>Material:</strong> {product.material}
      </p>
      {product.genero && (
        <p>
          <strong>Gênero:</strong> {product.genero}
        </p>
      )}
      <p>
        <strong>Loja:</strong> {product.loja?.nome}
      </p>
      <p>
        <strong>Referência:</strong> {product.referencia}
      </p>

      <h3>Variações</h3>
      {product.variacoes?.length ? (
        <ul>
          {product.variacoes.map((v: any, i: number) => (
            <li key={i}>
              <p>
                <strong>Descrição:</strong> {v.descricao_variacao}
              </p>
              <p>
                <strong>Quantidade:</strong> {v.quant_variacao}
              </p>
              <p>
                <strong>Valor:</strong> R$ {v.valor.toFixed(2)}
              </p>
            </li>
          ))}
        </ul>
      ) : (
        <p>Sem variações disponíveis.</p>
      )}

      <button className="btn btn-secondary mt-4" onClick={() => router.back()}>
        Voltar
      </button>
    </div>
  );
};

export default ProductDetail;
